@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700;800&family=Roboto:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* google fonts  */

body{
  font-family: 'Roboto',
  sans-serif !important;
}

h1,h2,h3{
  font-family: 'Playfair Display',
  serif !important;
}
.nav-link a{
    margin-left: 30px;
    color: black;
    text-decoration: none;
}
nav{
    background-color: #fbfbfb;
}
.login{
    color: white !important;
    text-decoration: none;
}
.dropdown {
    height: 50px;

}
.dropdown img{
    height: 40px;
    width: 40px;
    border-radius: 50%;
}
.dropdown-toggle.btn {
 padding: 0 10px;
} .btn-primary{
    background:#ca940d42 !important;
    border: none;
}
.dropdown a{
    text-decoration: none;
    color: black;
}
.banner{
    position: relative;
}
.banner img{
    width: 100%;
}
.hero-text{
    position: absolute;
    top: 50%;
    left: 50%;
    text-align: center;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: white;
}
.hero-text h1{
    font-weight: bold;
    font-size: 64px;
    line-height: 85px;
}
.button-primary{
    width: 200px;
    height: 60px;
    background: #C6A349;
    border-radius: 12px;
    color: white;
}

@media only screen and (max-width: 600px) {
   .hero-text h1 {
        font-size: 30px;
        line-height: 42px;
    }
    .hero-text p{
        font-size: 14px;
        font-weight: 300;
    }
    .banner img{
        height: 100vh;
        object-fit: cover;
    }
}
.destinations{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 30px;
}
.destinations img {
    width: 100%;
}

.destinations-area{
    margin-top: 50px;
    margin-bottom: 50px;
}
.destinations-area h2{
    margin-bottom: 50px;
    text-align: center;
}

@media only screen and (max-width: 600px) {
    .destinations {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 30px;
    }
}

@media only screen and (min-width: 600px) {
    .destinations {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 30px;
    }
}

@media only screen and (min-width: 768px) {
    .destinations {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 30px;
    }
}
@media only screen and (min-width: 992px) {
    .destinations {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 30px;
    }
}
.fact{
    margin-top: 60px;
    padding: 60px 0;
}
.fact-bg{
    background: url(/static/media/map-pattern.3697f041.png);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
}
.single-fact{
    text-align: center;
}
.single-fact i{
    width: 40px;
    height: 40px;
    background-color:#C6A349;
    display: inline-block;
    line-height: 40px;
    color: white;
    font-size: 18px;
    border-radius: 50%;
    margin-bottom: 10px;
}
.single-fact h4{
    font-size: 30px;
    font-weight: 500;
}
.single-fact h5{
    font-size: 16px;
}

.button-secondary{
    width: 116px;
    height: 44px;
    background: #C6A349;
    border-radius: 6px;
    color: white;
}
.single-service img{
    border-radius: 10px 10px 0 0;
    height: 230px;
    object-fit: cover;
}
.single-service{
    background: #FFFFFF;
    box-shadow: 0px 4px 30px 1px rgba(96, 96, 96, 0.1);
    border-radius: 10px;
}
.single-service .content h4{
    font-style: normal;
    font-size: 18px;
    line-height: 19px;
    color: #000000;
}
.single-service .content {
    padding: 15px;
}
.services{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
}
.services-area {
    margin-top: 50px;
    margin-bottom: 50px;
}
.services-area h2{
    margin-bottom: 40px;
    text-align: center;
}
@media only screen and (max-width: 600px) {
    .services {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 30px;
    }
}


@media only screen and (min-width: 600px){
     .services {
         display: grid;
         grid-template-columns: repeat(2, 1fr);
         grid-gap: 30px;
     }
}
@media only screen and (min-width: 768px) {
     .services {
         display: grid;
         grid-template-columns: repeat(2, 1fr);
         grid-gap: 30px;
     }
}
@media only screen and (min-width: 992px) {
     .services {
         display: grid;
         grid-template-columns: repeat(3, 1fr);
         grid-gap: 30px;
     }
}
.subscribe{
    position: relative;
    margin-top: 80px;
}
.subscribe form{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.subscribe form input{
    height: 60px;
    width: 300px;
    padding-left: 20px;
}
.footer{
    background: url(/static/media/foot-bg.c5ff9522.png);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 60px 0;
}
.footer ul li:not(:last-child){
    list-style: none;
    margin-bottom: 10px;
    color: white;
}
.footer ul{
    margin-left: 0;
    padding: 0;
}
.footer ul li a{
    color: white;
    text-decoration: none;
    font-size: 16px;
    font-weight: 300;
}
.footer p{
    color: white;
    font-size: 16px;
    font-weight: 300;
}
.footer h6{
    color: white;
}
