    background: #FFFFFF;
    box-shadow: 0px 4px 30px 1px rgba(96, 96, 96, 0.1);
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding:30px;
    margin: 50px;
}
